  --disactive-link-color: rgba(17, 17, 17, .5);
  --icon-shop-white: grayscale(100%);
  --book-modal-color: #fff;
  --shopping-list-text: rgba(0, 0, 0, .6);
  --main-font: "DM Sans", sans-serif;
  --animation: 250ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

button {
  cursor: pointer;
  font-family: inherit;
  font-style: inherit;
  border: none;
}

a {
  text-decoration: none;
}

body {
  font-family: var(--main-font);
  background-color: var(--body-bg-color);
  margin: 0;
}

[class$="-container"] {
  margin: 0 auto;
}

[data-theme="dark"] {
  --black-color: #fff;
  --grey-color: #202024;
  --purple-color: #eac645;
  --gold-color: #4f2ee8;
  --white-color: #111;
  --body-bg-color: #202024;
  --button-text-color: #111;
  --books-title-color: #b4afaf;
  --categories-list-color: rgba(255, 255, 255, .6);
  --disactive-link-color: rgba(246, 246, 246, .5);
  --icon-shop-white: invert(100%);
  --book-modal-color: #202024;
  --shopping-list-text: rgba(255, 255, 255, .6);
}

@media screen and (min-width: 375px) {
