:before, :after {
  box-sizing: border-box;
}

.mask {
  width: 100%;
  height: 100%;
  z-index: 100;
  background-color: rgba(255, 255, 255, .96);
  justify-content: center;
  align-items: center;
  transition: all 1s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: fixed;
  top: 0;
}

.wrapper {
  position: absolute;
  top: -35px;
  transform: scale(1.5);
}

.loader {
  height: 25px;
  width: 1px;
  animation: rotate 3.5s linear infinite;
  position: absolute;
}

.loader .dot {
  height: 20px;
  width: 17px;
  background: #502ee8;
  border-radius: 50%;
  position: relative;
  top: 60px;
}

@keyframes rotate {
  30% {
    transform: rotate(220deg);
  }

  40% {
    opacity: 1;
    transform: rotate(450deg);
  }

  75% {
    opacity: 1;
    transform: rotate(720deg);
  }

  76% {
    opacity: 0;
  }

  100% {
    opacity: 0;
    transform: rotate(0deg);
  }
}

.loader:nth-child(1) {
  animation-delay: .15s;
}

.loader:nth-child(2) {
  animation-delay: .3s;
}

.loader:nth-child(3) {
  animation-delay: .45s;
}

.loader:nth-child(4) {
  animation-delay: .6s;
}

.loader:nth-child(5) {
  animation-delay: .75s;
}

.loader:nth-child(6) {
  animation-delay: .9s;
}

.hide {
  opacity: 0;
  visibility: hidden;
}

.visible {
  opacity: 1;
  visibility: visible;
}

