input[type="checkbox"] {
  display: none;
}

.check {
  width: 40px;
  height: 24px;
  cursor: pointer;
  transition: background var(--animation);
  background: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 20px;
  display: block;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.check:after {
  content: "";
  width: 18px;
  height: 18px;
  transition: var(--animation);
  background: linear-gradient(#fff 0%, #f7f7f7 100%);
  border-radius: 50%;
  position: absolute;
  top: 3px;
  left: 1px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, .1), 0 2px 2px rgba(0, 0, 0, .1), 1px 2px 4px rgba(0, 0, 0, .1);
}

input[type="checkbox"]:checked ~ .check {
  transition: background var(--animation);
  background: linear-gradient(#4f2ee8 0%, #686868 100%);
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

input[type="checkbox"]:checked ~ .check:after {
  transform: translateX(20px);
}

